.section-contacts {
    padding: 45px 0;
    background: url(../img/map-bg.jpg) no-repeat 50%;
    @media (max-width: $breakpoint-small-max) {
        padding: 13px 0 25px;
    }
    @media (max-width: $breakpoint-xsmall-max) {
        padding: 0;
    }
}

.section-contacts-grid {
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;
    margin-left: -32px;
    @media (max-width: $breakpoint-small-max) {
        margin-left: -21px;
    }
}

.section-contacts__item {
    width: 50%;
    padding-left: 32px;
    @media (max-width: $breakpoint-small-max) {
        padding-left: 21px;
    }
    @media (max-width: $breakpoint-xsmall-max) {
        width: 100%;
        &:first-child {
            order: 1;
        }
    }
}

.contacts {
    padding: 40px 70px 37px;
    background-color: rgba(54, 57, 65, .8);
    @media (max-width: $breakpoint-small-max) {
        padding: 29px 29px 40px;
    }
    @media (max-width: $breakpoint-xsmall-max) {
        margin-left: -10px;
        margin-right: -10px;
        background-color: #363941;
        padding: 32px 9px 15px;
    }
}

.contacts__title {
    color: #ffffff;
    font-family: "Next Art", sans-serif;
    font-size: 30px;
    font-weight: 700;
    line-height: 1;
    text-transform: uppercase;
    letter-spacing: 5.94px;
    @media (max-width: $breakpoint-xsmall-max) {
        font-size: 24px;
        letter-spacing: 4.75px;
    }
}

.contacts__text {
    border-bottom: 1px solid rgba(199, 199, 199, .2);
    padding-bottom: 34px;
    margin-top: 37px;
    color: #f6f7fb;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.4px;
    a {
        color: inherit;
        text-decoration: underline;
    }
    p {
        margin: 21px 0 0;
        &:first-child {
            margin-top: 0;
        }
    }
    @media (max-width: $breakpoint-small-max) {
        margin-top: 35px;
        p {
            margin-top: 15px;
        }
    }
    @media (max-width: $breakpoint-xsmall-max) {
        font-size: 14px;
        margin-top: 31px;
        padding-bottom: 28px;
        p {
            margin-top: 18px;
        }
    }
}

.contacts__time {
    color: #8792a4;
    font-size: 16px;
    letter-spacing: 0.4px;
    line-height: 1;
    margin-top: 37px;
    svg {
        vertical-align: middle;
        margin-right: 7px;
        position: relative;
        top: -2px;
    }
    @media (max-width: $breakpoint-small-max) {
        font-size: 14px;
        margin-top: 42px;
        svg {
            width: 16px;
            height: 16px;
            margin-right: 5px;
        }
    }
    @media (max-width: $breakpoint-xsmall-max) {
        margin-top: 18px;
        svg {
            width: 25px;
            height: 25px;
        }
    }
}

.contacts-form {
    display: grid;
    grid-row-gap: 22px;
    grid-column-gap: 12px;
    @media (max-width: $breakpoint-small-max) {
        grid-row-gap: 4px;
    }
    @media (max-width: $breakpoint-xsmall-max) {
        background: url(../img/map-bg.jpg) no-repeat 50%;
        background-size: auto 100%;
        margin: 0 -10px;
        padding: 24px 10px 41px;
        grid-row-gap: 12px;
    }
}

.contacts-form__row {
    @media (min-width: $breakpoint-medium) {
        &:nth-child(1) {
            grid-column: 1 / 2;
            grid-row: 1 / 2;
        }
        &:nth-child(2) {
            grid-column: 1 / 2;
            grid-row: 2 / 3;
        }
        &:nth-child(3) {
            grid-column: 1 / 2;
            grid-row: 3 / 4;
        }
        &:nth-child(4) {
            grid-column: 2 / 3;
            grid-row: 1 / 4;
        }
        &:nth-child(5) {
            grid-column: 2 / 3;
            grid-row: 4 / 5;
            display: flex;
            align-items: center;
        }
        &:nth-child(6) {
            grid-column: 1 / 2;
            grid-row: 4 / 5;
        }
    }
}

.form-input {
    height: 48px;
    border: 2px solid #6c7483;
    background-color: #ffffff;
    color: #505661;
    font-size: 14px;
    font-weight: 400;
    font-family: inherit;
    line-height: 1;
    letter-spacing: 0.35px;
    padding: 0 20px;
    width: 100%;
    display: block;
}

.form-textarea {
    height: 48px;
    border: 2px solid #6c7483;
    background-color: #ffffff;
    color: #505661;
    font-size: 14px;
    font-weight: 400;
    font-family: inherit;
    line-height: 1;
    letter-spacing: 0.35px;
    padding: 15px 20px;
    width: 100%;
    display: block;
}

.form-submit {
    display: inline-block;
    color: #fefeff;
    font-family: "Next Art", sans-serif;
    font-size: 16px;
    font-weight: 700;
    line-height: 21.6px;
    text-transform: uppercase;
    letter-spacing: .8px;
    border: 3px solid #d52437;
    line-height: 50px;
    height: 56px;
    border-radius: 28px;
    background: transparent;
    cursor: pointer;
    padding-right: 90px;
    padding-left: 35px;
    position: relative;
    overflow: hidden;
    span {
        &:nth-child(1) {
            position: absolute;
            z-index: 1;
            width: calc(100% - 64px);
            height: 100%;
            left: 0;
            top: 0;
            background: #d52437;
            transition: width .3s ease, background .8s ease;
        }
        &:nth-child(2) {
            position: relative;
            z-index: 2;
        }
    }
    &::before {
        content: '';
        right: 61px;
        top: 0;
        height: 100%;
        position: absolute;
        width: 3px;
        background: #d52437;
        z-index: 2;
    }
    &::after {
        content: '';
        width: 10px;
        height: 10px;
        position: absolute;
        right: 30px;
        top: 50%;
        transform: translateY(-50%) rotate(-45deg);
        border-right: 3px solid #d52437;
        border-bottom: 3px solid #d52437;
        z-index: 2;
        transition: border-color .3s ease;
    }
    &:hover {
        span:nth-child(1) {
            width: 100%;
            background: #f9152e;
        }
        &::after {
            border-right-color: #fff;
            border-bottom-color: #fff;
        }
    }
}

.form-checkbox {
}

.contacts-form__rules {
    position: relative;
    color: #404040;
    font-size: 12px;
    text-decoration: underline;
    line-height: 1.25;
    padding-left: 28px;
    display: block;
    cursor: pointer;
    margin-left: 4px;
    @media (min-width: $breakpoint-small) and (max-width: $breakpoint-small-max) {
        margin-top: 5px;
        margin-bottom: 5px;
    }
    a {
        color: inherit;
        text-decoration: underline;
        &:hover {
            color: #000000;
        }
    }
    input {
        display: none;
    }
    span {
        position: absolute;
        left: 0;
        top: 0;
        width: 18px;
        height: 18px;
        border: 1px solid #0a0f37;
        background-color: #ffffff;
        border-radius: 2px;
    }
    input:checked + span::before {
        content: '';
        height: 3px;
        width: 8px;
        border-left: 3px solid #0a0f37;
        border-bottom: 3px solid #0a0f37;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%) rotate(-45deg);
        margin-top: -1px;
    }
}