}

/* Кнопка "Подробнее" */

.header-slider .slide-desc a {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-decoration: none;
}

.header-slider .slide-desc a p {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 73px;
    padding: 0 38px;
    line-height: 68px;
    border: 4px solid #d52437;
    border-top-left-radius: 36px;
    border-bottom-left-radius: 36px;
    color: #fff;
    font-family: "Next Art", sans-serif;
    font-weight: bold;
    font-size: 18px;
    text-decoration: none;
    letter-spacing: 0.2em;
}

.header-slider .slide-desc a:hover p,
.header-slider .slide-desc a:hover span {
    background-color: #ed3947;
}

.header-slider .slide-desc a:active p,
.header-slider .slide-desc a:active span {
    background-color: #f9152e;
}

.header-slider .slide-desc a span {
    position: relative;
    display: block;
    width: 69px;
    height: 73px;
    background-color: #d52437;
    border-top-right-radius: 36px;
    border-bottom-right-radius: 36px;
}

.header-slider .slide-desc a:hover span {
    border: 4px solid #d52437;
    border-left: none;
}

.slide-desc a span::before,
.slide-desc a span::after,
.swiper-button-prev::before,
.swiper-button-prev::after,
.swiper-button-next::before,
.swiper-button-next::after {
    position: absolute;
    left: 20px;
    display: block;
    content: "";
    width: 15px;
    height: 5px;
    background-color: #fff;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.slide-desc a span::before {
    top: calc(50% - 4px);
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
}

.slide-desc a span::after {
    top: calc(50% + 4px);
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

/* Контейнер для Pagination и Arrows */
.header-slider .container {
    position: relative;
    top: -715px;
    height: 100%;
}

/* Pagination */
.header-slider .swiper-pagination {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
    top: 50%;
    left: 125px !important;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    -webkit-transform: translate(0, -50%);
        -ms-transform: translate(0, -50%);
            transform: translate(0, -50%);
}

.header-slider .swiper-pagination-bullet {
    position: relative;
    left: -5px;
    margin: 14px 0 !important;
    width: 11px;
    height: 11px;
    background-color: #f0eeec;
    opacity: 1;
}

.header-slider .swiper-pagination-bullet-active {
    position: relative;
    left: 0;
    width: 21px;
    height: 21px;
    background-color: transparent;
    border: 2px solid #ff394f;
}

/* Arrows */
.header-slider .swiper-button-prev,
.header-slider .swiper-button-next {
    left: 2px;
    top: unset !important;
    bottom: 60px;
    width: 30px;
    height: 4px;
    background-color: #fff;
    background-image: none;
}

.swiper-button-prev::before {
    left: -2px;
    top: calc(50% - 7px);
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.swiper-button-prev::after {
    left: -2px;
    top: calc(50% + 1px);
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
}

.header-slider .swiper-button-next {
    left: 186px;
}

.swiper-button-next::before {
    top: calc(50% - 7px);
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
}

.swiper-button-next::after {
    top: calc(50% + 1px);
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.header-slider .swiper-button-disabled {
    opacity: 1 !important;
}

