.objects-slider {
    position: relative;
    width: 100%;
    user-select: none;
    margin: 37px 0 40px;
    position: relative;
    @media (max-width: $breakpoint-xsmall-max) {
        margin-top: 20px;
        margin-bottom: 20px;
    }
}

.objects-slider-frame {
    position: relative;
    width: 100%;
    margin: 0 auto;
    font-size: 0;
    line-height: 0;
    overflow: hidden;
    white-space: nowrap;
}

.objects-slider-slides {
    display: inline-block;
    padding: 0;
    margin: 0;
}

.objects-slider-slide {
    position: relative;
    display: inline-block;
    width: $container-width / 3 - 80px / 3;
    margin-right: 40px;
    white-space: normal;
    line-height: 1;
    @media (max-width: $container-width + $container-padding) {
        width: calc(100vw / 3 - (112px / 3)); // 112px отступ контейнера + оступ между элементами + ширина скролла страницы
    }
    @media (max-width: $breakpoint-medium-max) {
        margin-right: 16px;
        width: calc(100vw / 3 - (64px / 3)); // 64px отступ контейнера + оступ между элементами + ширина скролла страницы
    }
    @media (max-width: $breakpoint-small-max) {
        margin-right: 16px;
        width: calc(100vw / 2 - (48px / 2)); // 48px отступ контейнера + оступ между элементами + ширина скролла страницы
    }
    @media (max-width: $breakpoint-xsmall-max) {
        margin-right: 16px;
        width: 100vw;
    }
}

.objects-slider-nav {
    position: absolute;
    right: 0;
    top: -91px;
    display: flex;
    @media (max-width: $breakpoint-xsmall-max) {
        position: relative;
        margin: 0 auto 22px;
        width: 64px;
        top: initial;
        right: initial;
    }
}

.objects-slider-prev,
.objects-slider-next {
    display: block;
    padding: 0;
    border: 2px solid #bcbcbc;
    text-align: center;
    width: 32px;
    height: 32px;
    background: transparent;
    color: #ffffff;
    position: relative;
    cursor: pointer;
    &:hover {
        background: #acacad;
        color: #000000;
    }
    &::before {
        content: '';
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%) rotate(-45deg);
        width: 8px;
        height: 8px;
        border: 0 solid currentColor;
    }
}

.objects-slider-prev {
    border-radius: 16px 0 0 16px;
    border-right-width: 1px;
    &::before {
        border-left-width: 2px;
        border-top-width: 2px;
        margin-left: 4px;
    }
}

.objects-slider-next {
    border-radius: 0 16px 16px 0;
    border-left-width: 1px;
    &::before {
        border-right-width: 2px;
        border-bottom-width: 2px;
        margin-left: -4px;
    }
}

.project-item {
